<template>
  <q-page padding class="row justify-center">
    <div style="padding: 20px;width: 500px; max-width: 90vw;">
      <q-field 
        :count="30"
      >
        <q-input 
          v-model="post.name"
          float-label="Name"
          color="cyan accent-4"
          @blur="$v.post.name.$touch"
          @keyup.enter="submit"
          :error="$v.post.name.$error"
        />
      </q-field>
      <br>
      <q-field 
        :count="70"
      >
        <q-input 
          v-model="post.excerpt"
          float-label="Excerpt"
          color="cyan accent-4"
          @blur="$v.post.excerpt.$touch"
          @keyup.enter="submit"
          :error="$v.post.excerpt.$error"
        />
      </q-field>
      <br>
      <q-select
        radio
        float-label="Staus"
        v-model="post.status"
        :options="statusOptions"
      />
      <br>
      <q-select
        filter
        radio
        color="cyan accent-4"
        float-label="Categories"
        v-model="post.category_id"
        :options="categoriesLists"
        @blur="$v.post.category_id.$touch"
        @keyup.enter="submit"
        :error="$v.post.category_id.$error"
      />
      <br>
      <q-select
        multiple
        filter
        chips
        color="cyan accent-4"
        float-label="Tags"
        v-model="post.tags"
        :options="tagsLists"
        @blur="$v.post.tags.$touch"
        @keyup.enter="submit"
        :error="$v.post.tags.$error"
      />
      <br>
      <q-field>
        <q-input 
          v-model="post.file"
          float-label="File"
          color="cyan accent-4"
          @blur="$v.post.file.$touch"
          @keyup.enter="submit"
          :error="$v.post.file.$error"
        />
      </q-field>
      <br>
      <p class="caption">Body</p>
      <q-editor
        v-model="post.body"
        toolbar-text-color="black"
        toolbar-toggle-color="cyan accent-4"
        toolbar-flat
        toolbar-bg="white"
        :toolbar="[
          ['bold', 'italic', 'underline'],
          ['token', 'hr', 'link', 'custom_btn'],
          [
            {
              label: $q.i18n.editor.fontSize,
              icon: $q.icon.editor.fontSize,
              fixedLabel: true,
              fixedIcon: true,
              list: 'no-icons',
              options: ['size-1', 'size-2', 'size-3', 'size-4', 'size-5','size-6']
            }
          ],
          ['quote', 'unordered', 'ordered'],
          [
            {
              label: $q.i18n.editor.align,
              icon: $q.icon.editor.align,
              fixedLabel: true,
              list: 'only-icons',
              options: ['left', 'center', 'right', 'justify']
            },
          ],
        ]"
      />
      <br>
      <q-btn 
        color="cyan accent-4" 
        class="full-width" 
        label="Guardar cambios" 
        @click.prevent="updatePost()" 
      />
      <br>
      <br>
      <q-btn color="red"
       v-close-overlay 
       label="Cancelar"
       class="full-width"
       outline
       @click="$router.push({ name: 'ViewPost', params: post.slug })"
      />
    </div>
  </q-page>
</template>

<script>
  import { required, maxLength } from "vuelidate/lib/validators"
  export default {
    props:{
      "slug":{
        type:String,
        required:true
      }
    },
    data () {
        return {
            post:{
              id:0,
              name:"",
              excerpt:"",
              body:"",
              status:"DRAFT",
              slug:"",
              file:"",
              category_id:"",
              tags:[]               
            },
            statusOptions:[
              {
                label: "DRAFT",
                value: "DRAFT"
              },
              {
                label: "PUBLISHED",
                value: "PUBLISHED"
              }
            ],
            categoriesLists:[],
            tagsLists:[],
            text:""
        }
    },
    validations: {
      post: {
          name: {
            required,
            maxLength: maxLength(30)
          },
          excerpt: {
            required,
            maxLength: maxLength(70)
          },
          category_id: {
            required
          },
          tags: {
            required
          },
          body: {
            required
          },
          file: {
            required
          }
      }
    },
    created () {
      this.$q.loading.show();
      this.showPost();
      this.getCategories();
      this.getTags();
    },
    methods: {
      showPost () {
        var url = "https://enmonagas.000webhostapp.com/api/edit/post/" + this.slug;
        var self = this;
        $.ajax({
            type: "GET",
            url: url,
            headers: {
                'Content-Type': 'application/x-www-form-urlencoded'
            },
            async:true,
            success: function(data, status, xhr) {
                if(xhr.status !== 200){
                  self.$q.notify(status +':'+ xhr.status)
                  return;
                }

                self.post.id           =  data.post.id;
                self.post.name         =  data.post.name;
                self.post.excerpt      =  data.post.excerpt;
                self.post.body         =  data.post.body;
                self.post.status       =  data.post.status;             
                self.post.slug         =  data.post.slug;
                self.post.file         =  data.post.file;
                self.post.category_id  =  data.post.category_id;
                self.post.tags         =  data.tags;
                self.$q.loading.hide();     
            },
            error: function (error) { 
              self.$q.notify(error)
            } 
        });   
      },
      getCategories () {
        var url = "https://enmonagas.000webhostapp.com/api/categories/lists";   
        var self = this;
        $.ajax({
            type: "GET",
            url: url,
            headers: {
                'Content-Type': 'application/x-www-form-urlencoded'
            },
            async:true,
            success: function(data, status, xhr) {
                if(xhr.status !== 200){
                  self.$q.notify(status +':'+ xhr.status)
                  return;
                }
                self.categoriesLists = data    
            },
            error: function (error) { 
              self.$q.notify(error)
            } 
        }); 
      },
      getTags () {
        var url = "https://enmonagas.000webhostapp.com/api/tags/lists";
        var self = this; 
        $.ajax({
            type: "GET",
            url: url,
            headers: {
                'Content-Type': 'application/x-www-form-urlencoded'
            },
            async:true,
            success: function(data, status, xhr) {
                if(xhr.status !== 200){
                  self.$q.notify(status +':'+ xhr.status)
                  return;
                }
                self.tagsLists = data    
            },
            error: function (error) { 
              self.$q.notify(error)
            } 
        });
      },
      updatePost () {

        this.$v.post.$touch()

        if(this.$v.post.$error) {
          this.$q.notify("Please review fields again")
          return;
        }

       var url = "https://enmonagas.000webhostapp.com/api/update/post/" + this.post.id;
       var self = this; 
       $.ajax({
            type: "POST",
            url: url,
            data:{
                name:        self.post.name,
                excerpt:     self.post.excerpt,
                body:        self.post.body, 
                status:      self.post.status, 
                slug:        self.post.name,
                category_id: self.post.category_id,
                tags:        self.post.tags,
                file:        self.post.file 
            },
            headers: {
                'Content-Type': 'application/x-www-form-urlencoded'
            },
            async:true,
            success: function(data, status, xhr) {
                if(xhr.status !== 200){
                  self.$q.notify(status +':'+ xhr.status)
                  return;
                }
                self.notify(data, "positive", "check");
                self.$router.push("/posts");
            },
            error: function (error) { 
              self.$q.notify(error)
            } 
        });
      },
      notify (message,type,icon) {
        this.$q.notify({
          type: type,
          icon: icon,
          position: "top-left",
          message: message
        });
      }
    }
  }
</script>
<style>
  /**/
</style>


