import Vue        from "vue"
import VueRouter  from "vue-router"
import Layout     from "./components/layouts/Layout.vue"
import LayoutBack from "./components/layouts/Layout-back.vue"
import Home       from "./components/Home.vue"
import Wellcome   from "./components/Wellcome.vue"
import Login      from "./components/auth/Login.vue"
//Posts
import Posts      from "./components/pages/Post/Posts.vue"
import ViewPost   from "./components/pages/Post/View-post.vue"
import CreatePost from "./components/pages/Post/Create-post.vue"
import EditPost   from "./components/pages/Post/Edit-post.vue"

//Categories
import Categories     from "./components/pages/Category/Categories.vue"
import CreateCategory from "./components/pages/Category/Create-category.vue"
import EditCategory   from "./components/pages/Category/Edit-category.vue"

//Tags
import Tags       from "./components/pages/Tag/Tags.vue"
import CreateTag  from "./components/pages/Tag/Create-tag.vue"
import EditTag    from "./components/pages/Tag/Edit-tag.vue"

Vue.use(VueRouter)

const router = new VueRouter({
  mode:"history",
  routes: [
    {
      path: "/",
      name:"Layout",
      component: Layout,
      children: [
        {
          path: "/wellcome", 
          name: "Wellcome",
          component: Wellcome, 
          meta:{ 
              title: "wellcome",
              forVisitors:true
          }
        },
        {
          path: "/home", 
          name: "Home",
          component: Home, 
          meta:{ 
              title: "home",
              forAuth: true
          }
        },
        {
          path: "/posts",
          name: "Posts",
          component: Posts,
          meta: {
            title: "posts",
            forAuth: true
          }
        },
        {
          path: "/categories",
          name: "Categories",
          component: Categories,
          props:true,
          meta: {
            title: "categories",
            forAuth: true
          }
        },
        {
          path: "/tags",
          name: "Tags",
          component: Tags,
          props:true,
          meta: {
            title: "tags",
            forAuth: true
          }
        },
        {
          path: "/login",
          name:"Login",
          component: Login, 
          meta:{ 
              title: "login",
              forVisitors: true
          }
        }
      ]
    },
    {
      path: "/layout/back", 
      name:"LayoutBack",
      component: LayoutBack,
      children: [
        {
          path: "/post/:slug",
          name: "ViewPost",
          component: ViewPost,
          props:true,
          meta: {
            title: "View post",
            forAuth: true
          }
        },
        {
          path: "/create/post",
          name: "CreatePost",
          component: CreatePost,
          meta: {
            title: "Create post",
            forAuth: true
          }
        },
        {
          path: "/edit/post/:slug",
          name: "EditPost",
          component: EditPost,
          props: true,
          meta: {
            title: "Edit post",
            forAuth: true
          }
        },
        {
          path: "/create/category",
          name: "CreateCategory",
          component: CreateCategory,
          meta: {
            title: "Create category",
            forAuth: true
          }
        },
        {
          path: "/edit/category/:slug",
          name: "EditCategory",
          component: EditCategory,
          props: true,
          meta: {
            title: "Edit category",
            forAuth: true
          }
        },
        {
          path: "/create/tag",
          name: "CreateTag",
          component: CreateTag,
          meta: {
            title: "Create tag",
            forAuth: true
          }
        },
        {
          path: "/edit/tag/:slug",
          name: "EditTag",
          component: EditTag,
          props: true,
          meta: {
            title: "Edit tag",
            forAuth: true
          }
        }
      ]
    }
  ]
})
export default router



