<template>
  <q-page padding class="row item-start justify-center">
    <div style="width:500px; max-width: 95vw;">
      <q-list inset-separator class="q-mt-md">
        <q-search class="q-pa-md"
           icon="search" 
           color="cyan accent-4" 
           v-model="search" 
           clearable 
           placeholder="Search tag" 
           :error="searchTags.length == 0 ? true : false"
        />
        <template v-for="(tag, index) in searchTags">
          <q-item multiline>
            <q-item-side icon="style" color="cyan accent-4"/>
            <q-item-main>
               <q-item-tile label line="1">{{ tag.name }}</q-item-tile>
               <q-item-tile sublabel line="3" v-text="tag.body"></q-item-tile>
            </q-item-main>
            <q-item-side right>
              <q-btn flat round dense icon="more_vert" text-color="light">
                <q-popover>
                  <q-list link>
                    <q-item v-close-overlay :to="{ name: 'EditTag', params: { slug:tag.slug }}">
                      <q-item-main label="Edit Tag" />
                    </q-item>
                    <q-item v-close-overlay>
                      <q-item-main label="Delete tag" @click.native="deleteTag(tag)"/>
                    </q-item>
                  </q-list>
                </q-popover>
              </q-btn>
            </q-item-side>
          </q-item>
        </template>
      </q-list>
      <br>
      <q-pagination class="justify-center q-mt-md" 
        input
        v-model="page"
        :min="pagination.curent_page"
        :max="pagination.last_page"
        :ellipses="false"
        @click.native="pages()"
      />
    </div>
  </q-page>
</template>

<script>
 export default {
   data () {
       return {
          tags:[],
          pagination:{
            total:0,
            current_page:1,
            per_page:0,
            last_page:0,
            from:0,
            to:0
          },
          page:1,
          search:""
        }
     },
     created () {
        this.$q.loading.show();
        this.getTags();
     },
     methods:{
          getTags (page) {
            var url = "https://enmonagas.000webhostapp.com/api/tags?page=" + page;
            var self = this;
            $.ajax({
                type: "GET",
                url: url,
                headers: {
                    'Content-Type': 'application/x-www-form-urlencoded'
                },
                async:true,
                success: function(data, status, xhr) {
                    if(xhr.status !== 200){
                      self.$q.notify(status +':'+ xhr.status)
                      return;
                    }
                    self.tags        = data.tags.data,
                    self.pagination  = data.pagination
                    self.$q.loading.hide();     
                },
                error: function (error) { 
                  self.$q.notify(error)
                } 
            });
          },
          deleteTag (id) {
            this.$q.notify("Deleting tag..." + id);
          },
          pages () {
            this.getTags(this.page);
          } 
      },
      computed:{
        searchTags () {
          return this.tags.filter((tag) => tag.name.includes(this.search));
        }
      }
   }
</script>


