<template>
  <q-page padding class="row item-start justify-center">
    <div style="width:500px; max-width: 95vw;">
      <q-list highlight inset-separator class="q-mt-md">
        <q-search class="q-pa-md"
           icon="search" 
           color="cyan accent-4" 
           v-model="search" 
           clearable 
           placeholder="Search post" 
           :error="searchPosts.length == 0 ? true : false"
        />
        <template v-for="(post, index) in searchPosts">
          <q-item multiline :to="{ name: 'ViewPost', params: { slug: post.slug }}">
            <q-item-side icon="storage" color="cyan accent-4"/>
            <q-item-main>
               <q-item-tile label line="1">{{ post.name }}</q-item-tile>
               <q-item-tile sublabel line="3" v-text="post.excerpt"></q-item-tile>
            </q-item-main>
            <q-item-side right>
              <q-item-tile icon="navigate_next" />
            </q-item-side>
          </q-item>
        </template>
      </q-list>
      <br>
      <q-pagination class="justify-center q-mt-md" 
        input
        v-model="page"
        :min="pagination.curent_page"
        :max="pagination.last_page"
        :ellipses="false"
        @click.native="pages()"
      />
    </div>
  </q-page>
</template>

<script>
 export default {
    data () {
       return {
          posts:[],
          pagination:{
            total:0,
            current_page:1,
            per_page:0,
            last_page:0,
            from:0,
            to:0
          },
          page:1,
          search:""
       }
     },
     created () {
        this.$q.loading.show();
        this.getPosts();
     },
     methods:{
        getPosts (page) {
          var url = "https://enmonagas.000webhostapp.com/api/posts?page=" + page;
          var self = this;
          $.ajax({
              type: "GET",
              url: url,
              headers: {
                  'Content-Type': 'application/x-www-form-urlencoded'
              },
              async:true,
              success: function(data, status, xhr) {
                  if(xhr.status !== 200){
                    self.$q.notify(status +':'+ xhr.status)
                    return;
                  }
                  self.posts       = data.posts.data,
                  self.pagination  = data.pagination
                  self.$q.loading.hide();     
              },
              error: function (error) { 
                self.$q.notify(error)
              } 
          }); 
        },
        deletePost (id) {
          this.$q.notify("Deleting post..." + id);
        },
        pages () {
          this.getPosts(this.page);
        }
      },
      computed: {
        searchPosts () {
          return this.posts.filter((post) => post.name.includes(this.search));
        }
      }
   }
</script>
<style>
  .myTag {
    cursor: pointer;
  }
</style>


