<template>
  <q-page padding class="row justify-center">
    <div style="padding: 20px;width: 500px; max-width: 90vw;">
      <p class="text-center">
        <q-icon 
          name="vpn_key" 
          color="cyan accent-4" 
          style="font-size: 60px;" 
        />
      </p>
      <q-field
        error-label="Email must be a valid email address."
      >
       <q-input 
          v-model="user.email"
          float-label="Correo" 
          color="cyan accent-4"
          @blur="$v.user.email.$touch"
          @keyup.enter="submit"
          :error="$v.user.email.$error"
        />
      </q-field>
        <br>
      <q-field
        error-label="Password must have at least 6 letters"
      >
        <q-input 
          v-model="user.password" 
          type="password"
          float-label="Contrasea" 
          color="cyan accent-4"
          @blur="$v.user.password.$touch"
          @keyup.enter="submit"
          :error="$v.user.password.$error"
        />
      </q-field>
      <br>
      <q-btn 
        color="cyan accent-4" 
        class="full-width" 
        label="Accesar" 
        @click.prevent="login()" 
      />
    </div>
  </q-page>
</template>

<script>
import { required, email, minLength } from 'vuelidate/lib/validators'
export default {
  data () {
    return {
            user:{
              email:'',
              password:''
            }
        }
  },
  validations: {
    user: {
      email: { 
        required, 
        email
      },
      password: {
        required, 
        minLength: minLength(6)
      }
    }
  },
  methods: {
    login() {
      this.$v.user.$touch()

      if(this.$v.user.$error) {
        this.$q.notify('Please review fields again');
        return
      }

      var url = "https://enmonagas.000webhostapp.com/api/login";
      var self = this;
      $.ajax({
          type: "POST",
          url: url,
          data: { 
            "email" : self.user.email,
            "password" : self.user.password 
          },
          headers: {
              'Content-Type': 'application/x-www-form-urlencoded'
          },
          async:true,
          success: function(data, status, xhr) {
              if(xhr.status !== 200){
                self.$q.notify(status +':'+ xhr.status)
                return;
              }
              var token = data.token;
              self.$store.commit('login',{ 
                token: token, 
                isAuth: true, 
                userName: self.user.email 
              });
              self.$router.push("/posts");     
          },
          error: function (error) { 
            self.$q.notify("These credentials do not match our records");
          } 
      });
    }
  }
}
</script>
<style>
  .content {
    width: 100%;  
    min-height: 60vh;
    display: -webkit-box;
    display: -webkit-flex;
    display: -moz-box;
    display: -ms-flexbox;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    padding: 15px;
    background: #fff;  
  }
</style>





