<template>
  <q-page padding class="row justify-center">
        <div style="padding: 20px;width: 500px; max-width: 90vw;">
      <q-field 
        :count="30"
      >
        <q-input 
          v-model="category.name"
          float-label="Name"
          color="cyan accent-4"
          @blur="$v.category.name.$touch"
          @keyup.enter="submit"
          :error="$v.category.name.$error"
        />
      </q-field>
      <br>
      <q-field 
        :count="60"
      >
        <q-input 
           v-model="category.body" 
           rows="3" 
           float-label="Body" 
           type="textarea"
           @blur="$v.category.body.$touch"
           @keyup.enter="submit"
           :error="$v.category.body.$error"
        />
      </q-field>
      <br>
      <q-btn 
        color="cyan accent-4" 
        class="full-width" 
        label="Guardar categorys" 
        @click.prevent="updateCategory()" 
      />
      <br>
      <br>
      <q-btn color="red"
       v-close-overlay 
       label="Cancelar"
       class="full-width"
       outline
       to="/categories"
      />
    </div>
  </q-page>
</template>

<script>
  import { required, maxLength } from "vuelidate/lib/validators"
  export default {
    props:{
      "slug":{
        type:String,
        required:true
      }
    },
    data () {
        return {
          category:{
              id: 0,
              name:"",
              body:"",
              slug:"",        
          }
        }
    },
    validations: {
      category: {
          name: {
            required,
            maxLength: maxLength(30)
          },
          body: {
            required,
            maxLength: maxLength(60)
          }
      }
    },
    created () {
      this.$q.loading.show();
      this.getCategory();
    },
    methods: {
      getCategory () {

        var url = "https://enmonagas.000webhostapp.com/api/category/" + this.slug;
        var self = this;
        $.ajax({
            type: "GET",
            url: url,
            headers: {
                'Content-Type': 'application/x-www-form-urlencoded'
            },
            async:true,
            success: function(data, status, xhr) {
                if(xhr.status !== 200){
                  self.$q.notify(status +':'+ xhr.status)
                  return;
                }

                self.category  =  data.category
                self.$q.loading.hide();     
            },
            error: function (error) { 
              self.$q.notify(error)
            } 
        });  
      },
      updateCategory () {
        this.$v.category.$touch()

        if(this.$v.category.$error) {
          this.notify("Please review fields again","negative","error")
          return
        }

         var url = "https://enmonagas.000webhostapp.com/api/update/category/" + this.category.id;
         var self = this; 
         $.ajax({
              type: "POST",
              url: url,
              data:{
                  name:  self.category.name,
                  body:  self.category.body, 
                  slug:  self.category.name
              },
              headers: {
                  'Content-Type': 'application/x-www-form-urlencoded'
              },
              async:true,
              success: function(data, status, xhr) {
                  if(xhr.status !== 200){
                    self.$q.notify(status +':'+ xhr.status)
                    return;
                  }
                  self.notify(data, "positive", "check");
                  self.$router.push("/categories");
              },
              error: function (error) { 
                self.$q.notify(error)
              } 
          });
      },
      notify (message, type, icon) {
        this.$q.notify({
          type: type,
          icon: icon,
          position: "top-left",
          message: message
        });
      }

    }
  }
</script>
<style>
  /**/
</style>


