<template>
  <q-layout view="hHr LpR lFf">
    <q-layout-header>
      <q-toolbar color="cyan accent-4">
        <q-btn
          flat round dense
          @click="showLeft = !showLeft"
          icon="menu"
        />
        <q-toolbar-title>
           En Monagas
           <span slot="subtitle">Punta de mata</span>
        </q-toolbar-title>
          <q-btn-dropdown
            icon="person"
            flat
            dense
            v-if="state"
          >
            <q-list link>
              <q-item v-close-overlay @click.native="logout()">
                <q-item-side icon="person" color="green" />
                <q-item-main>
                  <q-item-tile label>{{ userName }}</q-item-tile>
                  <q-item-tile sublabel>Click to logout</q-item-tile>
                </q-item-main>
              </q-item>
              <q-item-separator />
              <q-item v-close-overlay to="/create/post">
                <q-item-side icon="storage" />
                <q-item-main>
                  <q-item-tile label>Posts</q-item-tile>
                  <q-item-tile sublabel>Create post</q-item-tile>
                </q-item-main>
              </q-item>
              <q-item v-close-overlay to="/create/category">
                <q-item-side icon="widgets" />
                <q-item-main>
                  <q-item-tile label>Category</q-item-tile>
                  <q-item-tile sublabel>Create category</q-item-tile>
                </q-item-main>
              </q-item>
              <q-item v-close-overlay to="/create/tag">
                <q-item-side icon="style" />
                <q-item-main>
                  <q-item-tile label>Tag</q-item-tile>
                  <q-item-tile sublabel>Create tag</q-item-tile>
                </q-item-main>
              </q-item>
            </q-list>
          </q-btn-dropdown>
      </q-toolbar>
      <q-tabs  animated color="cyan accent-4" align="justify" v-model="title">
        <q-route-tab slot="title" 
          name="wellcome" 
          icon="important_devices" 
          label="Wellcome" 
          v-if="! state"
          to="/wellcome"
          exact
        />
        <q-route-tab slot="title" 
         name="login"  
         icon="vpn_key" 
         label="Login" 
         v-if="! state"
         to="/login"
         exact
        />
        
        <!-- Protected -->

        <q-route-tab slot="title" 
          name="home"  
          icon="home" 
          label="Home" 
          v-if="state"
          to="/home"
          exact
        />
        <q-route-tab slot="title" 
          name="posts"  
          icon="storage" 
          label="Posts" 
          v-if="state"
          to="/posts"
          exact
        />
        <q-route-tab slot="title" 
          name="categories"  
          icon="widgets" 
          label="Categories" 
          v-if="state"
          to='/categories'
          exact
        />
        <q-route-tab slot="title" 
          name="tag"  
          icon="style" 
          label="Tag" 
          v-if="state"
          to='/tags'
          exact
        />
      </q-tabs>
    </q-layout-header>
    <q-layout-drawer side="left" v-model="showLeft">
      <q-list no-border link inset-separator>
        <q-list-header>
          <h4 class="">En monagas</h4>
        </q-list-header>
        <q-item to="/wellcome" v-if="! state">
          <q-item-side icon="important_devices"/>
          <q-item-main label="wellcome" sublabel="Inicio de mi app" />
        </q-item>
        <q-item to="/login" v-if="! state">
          <q-item-side icon="vpn_key"/>
          <q-item-main label="Login" sublabel="Inicia sesion" />
        </q-item>
        <q-item to="/home" v-if="state">
          <q-item-side icon="home"/>
          <q-item-main label="Home" sublabel="My app" />
        </q-item>
        <q-item to="/posts" v-if="state">
          <q-item-side icon="storage"/>
          <q-item-main label="Posts" sublabel="My posts" />
        </q-item>
        <q-item to="/categories" v-if="state">
          <q-item-side icon="widgets"/>
          <q-item-main label="Categories" sublabel="Categories list" />
        </q-item>
        <q-item to="/tags" v-if="state">
          <q-item-side icon="style"/>
          <q-item-main label="Tags" sublabel="Tags list" />
        </q-item>
      </q-list>
    </q-layout-drawer>
    <q-page-container>
       <router-view />
    </q-page-container>
    <q-layout-footer>
      <q-toolbar color="cyan accent-4">
        <q-toolbar-title>
          En monagas
          <span slot="subtitle">J-123456-9</span>
        </q-toolbar-title>
      </q-toolbar>
    </q-layout-footer>
  </q-layout>
</template>
<script>
import {mapState} from 'vuex';
export default {
  data () {
    return {
      title:'',
      showLeft: false,
      routes:[1,2,3,4,5],
    }
  },
  components: {
    /**/
  },
  watch: {
    '$route' () {
      this.title = this.$route.meta.title
    }
  },
  mounted () {
    this.title  = this.$route.meta.title
  },
  methods: {
    logout () {
      this.$store.commit('logout');
      localStorage.clear('mi-app');
      this.$router.push('/wellcome');
    }
  },
  computed:{
    state () {
      return this.$store.state.isAuth;
    },
    userName () {
      return this.$store.state.userName;
    }
  }
}
</script>
<style>
  /**/
</style>


