<template>
  <q-page padding class="row item-start justify-center">
    <div style="width:500px; max-width: 95vw;">
      <q-card inline class="bigger q-mt-md">
        <q-card-media class="image">
          <img :src="post.file">
        </q-card-media>
        <q-card-title>
           {{ post.name }}
           <div slot="subtitle">
              Category:&nbsp;<b>{{ category.name }}</b>
           </div>
           <q-btn round flat icon="more_vert" slot="right">
              <q-popover>
                <q-list link class="no-border">
                  <q-item v-close-overlay :to="{ name: 'EditPost', params: post.slug }">
                    <q-item-main label="Edit post"/>
                  </q-item>
                  <q-item v-close-overlay @click.native="deletePost(post.id)">
                    <q-item-main label="Remove post" />
                  </q-item>
                </q-list>
              </q-popover>
            </q-btn>
        </q-card-title>
        <q-card-main>
          <p class="text-faded" v-html="post.body"></p>
        </q-card-main>
        <q-card-actions>
         <p>
          <template v-for="tag in tags">
            <q-chip tag 
              class="myTag"
              color="light" 
              @click="showTag(tag.id)"
            >
              {{ tag.name }}
            </q-chip>
          </template>
         </p>
        </q-card-actions>
      </q-card>
    </div>
  </q-page>
</template>

<script>
 export default {
      props:{
        "slug":{
          type:String,
          required:true
        }
      },
      data ()  {
           return {
              post:{
                id:0,
                name:"",
                excerpt:"",
                body:"",
                status:"",               
                slug:"",
              },
              category:[],
              tags:[]
           }
       },
       created () {
          this.$q.loading.show();
          this.getPost();
       },
       methods:{
        getPost () {
          var url = "https://enmonagas.000webhostapp.com/api/post/" + this.slug;   
          fetch(url, {
              method: 'GET',
              headers: {
                'Content-Type': 'application/x-www-form-urlencoded'
              }  
          })
          .then(response => {
            if (response.status !== 200) {
                this.$q.notify('Something could be going wrong ' + response.status)
                return;
            }
            response.json()
            .then(data => {
                this.post      = data.post
                this.category  = data.category
                this.tags      = data.tags
                this.$q.loading.hide();
            });
          })
          .catch(error => {
             this.$q.notify(error);
          });
        },
        deletePost (id) {
          this.$q.notify("Deleting post..." + id);
        },
        showTag (id) {
          this.$q.notify("Este es el tags" + id,"negative","error");
        },
        notify (message,type,icon) {
          this.$q.notify({
            type: type,
            icon: icon,
            position: "top",
            detail: message
          });
        }
      } 
   }
</script>
<style>
  .myTag {
    cursor: pointer;
  }
  .image {
    width: 100%;
  }
</style>


