<template>
  <q-page padding class="row justify-center">
    <div style="padding: 20px;width: 500px; max-width: 90vw;">
      <q-field 
        :count="30"
      >
        <q-input 
          v-model="tag.name"
          float-label="Name"
          color="cyan accent-4"
          @blur="$v.tag.name.$touch"
          @keyup.enter="submit"
          :error="$v.tag.name.$error"
        />
      </q-field>
      <br>
      <q-field 
        :count="60"
      >
        <q-input 
           v-model="tag.body" 
           rows="3" 
           float-label="Body" 
           type="textarea"
           @blur="$v.tag.body.$touch"
           @keyup.enter="submit"
           :error="$v.tag.body.$error"
        />
      </q-field>
      <br>
      <q-btn 
        color="cyan accent-4" 
        class="full-width" 
        label="Guardar tags" 
        @click.prevent="saveTag()" 
      />
      <br>
      <br>
      <q-btn color="red"
       v-close-overlay 
       label="Cancelar"
       class="full-width"
       outline
       to="/categories"
      />
    </div>
  </q-page>
</template>

<script>
  import { required, maxLength } from "vuelidate/lib/validators"
  export default {
    data () {
        return {
            tag:{
              name:"",
              body:"",       
            }
        }
    },
    validations: {
      tag: {
          name: {
            required,
            maxLength: maxLength(30)
          },
          body: {
            required,
            maxLength: maxLength(60)
          }
      }
    },
    methods: {
      saveTag (tag) {
        this.$v.tag.$touch()
        if(this.$v.tag.$error) {
          this.notify("Please review fields again","negative","error")
          return
        }

        var url = "https://enmonagas.000webhostapp.com/api/create/tag";
        var self = this; 
        $.ajax({
            type: "POST",
            url: url,
            data:{
                name: self.tag.name,
                body: self.tag.body,
                slug: self.tag.name
            },
            headers: {
                'Content-Type': 'application/x-www-form-urlencoded'
            },
            async:true,
            success: function(data, status, xhr) {
                if(xhr.status !== 200){
                  self.$q.notify(status +':'+ xhr.status)
                  return;
                }
                self.notify(data, "positive", "check");
                self.$router.push("/tags");
            },
            error: function (error) { 
              self.$q.notify(error)
            } 
        });  
      },
      notify (message, type, icon) {
        this.$q.notify({
          type: type,
          icon: icon,
          position: "top-left",
          message: message
        });
      }
    }
  }
</script>
<style>
  /**/
</style>


