<template>
  <q-page padding class="row item-start justify-center">
    <div style="width:500px; max-width: 95vw;">
      <q-list inset-separator class="q-mt-md">
        <q-search class="q-pa-md"
           icon="search" 
           color="cyan accent-4" 
           v-model="search" 
           clearable 
           placeholder="Search category" 
           :error="searchCategories.length == 0 ? true : false"
        />
        <template v-for="(category, index) in searchCategories">
          <q-item multiline>
            <q-item-side icon="widgets" color="cyan accent-4"/>
            <q-item-main>
               <q-item-tile label line="1">{{ category.name }}</q-item-tile>
               <q-item-tile sublabel line="3" v-text="category.body"></q-item-tile>
            </q-item-main>
            <q-item-side right>
              <q-btn flat round dense icon="more_vert" text-color="light">
                <q-popover>
                  <q-list link>
                    <q-item v-close-overlay :to="{ name: 'EditCategory', params: { slug:category.slug }}">
                      <q-item-main label="Edit Category" />
                    </q-item>
                    <q-item v-close-overlay>
                      <q-item-main label="Delete category" @click.native="deleteCategory(category)"/>
                    </q-item>
                  </q-list>
                </q-popover>
              </q-btn>
            </q-item-side>
          </q-item>
        </template>
      </q-list>
      <br>
      <q-pagination class="justify-center q-mt-md" 
        input
        v-model="page"
        :min="pagination.curent_page"
        :max="pagination.last_page"
        :ellipses="false"
        @click.native="pages()"
      />
    </div>
  </q-page>
</template>

<script>
 export default {
   data () {
       return {
          categories:[],
          pagination:{
            total:0,
            current_page:1,
            per_page:0,
            last_page:0,
            from:0,
            to:0
          },
          page:1,
          search:""
        }
     },
     created () {
        this.$q.loading.show();
        this.getCategories();
     },
     methods:{
        getCategories (page) {
          var url = "https://enmonagas.000webhostapp.com/api/categories?page=" + page;
          var self = this;
          $.ajax({
              type: "GET",
              url: url,
              headers: {
                  'Content-Type': 'application/x-www-form-urlencoded'
              },
              async:true,
              success: function(data, status, xhr) {
                  if(xhr.status !== 200){
                    self.$q.notify(status +':'+ xhr.status)
                    return;
                  }
                  self.categories  = data.categories.data,
                  self.pagination  = data.pagination
                  self.$q.loading.hide();     
              },
              error: function (error) { 
                self.$q.notify(error)
              } 
          }); 
        },
        deleteCategory (id) {
          this.$q.notify("Deleting category..." + id);
        },
        pages () {
          this.getCategories(this.page);
        } 
      },
      computed:{
        searchCategories () {
          return this.categories.filter((category) => category.name.includes(this.search));
        }
      }
   }
</script>


